<template>
  <div>
    <VersionView02Home />
    <VersionView02Works />
    <VersionView02Skills />
    <VersionView02About />
    <VersionView02Footer />
  </div>
</template>

<script>
import '@/assets/styles/all.scss'
import { onMounted } from '@vue/runtime-core'
import VersionView02Home from '@/components/VersionView02Home'
import VersionView02Works from '@/components/VersionView02Works'
import VersionView02Skills from '@/components/VersionView02Skills'
import VersionView02About from '@/components/VersionView02About'
import VersionView02Footer from '@/components/VersionView02Footer'

export default {
  name: 'VersionView02',
  components: {
    VersionView02Home,
    VersionView02Works,
    VersionView02Skills,
    VersionView02About,
    VersionView02Footer
  },
  setup () {
    const backToTop = () => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    }

    let observer = null
    const intersectionObserverCallback = function (entries) {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.classList.add('active')
          observer.unobserve(entry.target)
        }
      })
    }

    onMounted(() => {
      observer = new IntersectionObserver(intersectionObserverCallback, { threshold: 0.6 })
      const targets = document.querySelectorAll('.js-show-on-scroll')
      targets.forEach(function (target) {
        target.classList.add('toFadeIn')
        observer.observe(target)
      })
    })

    return { backToTop }
  }
}
</script>

<style lang="scss">
.toFadeIn {
  opacity: 0;
  transition: all 0.5s;
  transform: translateY(20px);

  &.active {
    opacity: 1;
    transform: none;
  }
}
</style>
